.full-stars {
  position: absolute;
  left: 0;
  top: 0;
  white-space: nowrap;
  overflow: hidden;
  color: #FCAF29; }

.empty-stars:before,
.full-stars:before {
  content: "\2605\2605\2605\2605\2605";
  font-size: 18px; }

.empty-stars:before {
  -webkit-text-stroke: 1px #FCAF29; }

.full-stars:before {
  -webkit-text-stroke: 1px #FCAF29; }

.rating {
  background-color: #FFFBDE;
  border-radius: 20px;
  unicode-bidi: bidi-override;
